import React from "react";
import "./App.css";
import Button from "@mui/material/Button";
import InputLabel from "@mui/material/InputLabel";
import MenuItem from "@mui/material/MenuItem";
import FormControl from "@mui/material/FormControl";
import Select from "@mui/material/Select";
import { mockFloors, mockRooms } from "./mock";
import "react-datepicker/dist/react-datepicker.css";
import TextField from "@mui/material/TextField";
import { Formik } from "formik";
import { DatePickerField } from "./DatePickerField";

const ExampleCustomInput = React.forwardRef(({ value, onClick }, ref) => (
  <button type="button" className="custom-input" onClick={onClick} ref={ref}>
    {value}
  </button>
));

function App() {
  return (
    <div className="wrapper">
      <Formik
        initialValues={{
          tower: "",
          floor: "",
          room: "",
          startDate: new Date(),
          comment: "",
        }}
        onSubmit={(formValues) => {
          console.log(formValues);
        }}
      >
        {(formik) => {
          return (
            <form onSubmit={formik.handleSubmit}>
              <FormControl fullWidth style={{ margin: "10px 0" }}>
                <InputLabel id="demo-simple-select-label">Башня</InputLabel>
                <Select
                  MenuProps={{
                    style: {
                      maxHeight: 200,
                    },
                  }}
                  labelId="demo-simple-select-label"
                  label="Башня"
                  name="tower"
                  value={formik.values.tower}
                  onChange={formik.handleChange}
                >
                  <MenuItem value={"A"}>Башня А</MenuItem>
                  <MenuItem value={"Б"}>Башня Б</MenuItem>
                </Select>
              </FormControl>
              <FormControl fullWidth style={{ margin: "10px 0" }}>
                <InputLabel id="demo-simple-select-label">Этаж</InputLabel>
                <Select
                  MenuProps={{
                    style: {
                      maxHeight: 200,
                    },
                  }}
                  labelId="demo-simple-select-label"
                  label="Этаж"
                  name="floor"
                  value={formik.values.floor}
                  onChange={formik.handleChange}
                >
                  {mockFloors.map((floorEl) => (
                    <MenuItem value={floorEl.id} key={floorEl.id}>
                      {floorEl.title}
                    </MenuItem>
                  ))}
                </Select>
              </FormControl>
              <FormControl fullWidth style={{ margin: "10px 0" }}>
                <InputLabel id="demo-simple-select-label">Комната</InputLabel>
                <Select
                  MenuProps={{
                    style: {
                      maxHeight: 200,
                    },
                  }}
                  labelId="demo-simple-select-label"
                  label="Комната"
                  name="room"
                  value={formik.values.room}
                  onChange={formik.handleChange}
                >
                  {mockRooms.map((roomEl) => (
                    <MenuItem value={roomEl.id} key={roomEl.id}>
                      {roomEl.title}
                    </MenuItem>
                  ))}
                </Select>
              </FormControl>
              <FormControl fullWidth style={{ margin: "10px 0" }}>
                <DatePickerField
                  type="button"
                  name="startDate"
                  showTimeSelect
                  dateFormat="MMMM d, yyyy h:mm aa"
                  customInput={<ExampleCustomInput />}
                />
              </FormControl>
              <FormControl fullWidth style={{ margin: "10px 0" }}>
                <TextField
                  id="outlined-multiline-static"
                  label="Комментарий"
                  name="comment"
                  multiline
                  rows={4}
                  onChange={formik.handleChange}
                  value={formik.values.comment}
                />
              </FormControl>
              <FormControl fullWidth style={{ margin: "2px 0" }}>
                <Button
                  type="submit"
                  variant="outlined"
                  style={{ margin: "5px 0" }}
                >
                  Отправить
                </Button>
              </FormControl>
              <FormControl fullWidth style={{ margin: "2px 0" }}>
                <Button
                  type="button"
                  variant="outlined"
                  onClick={() => {
                    formik.resetForm();
                  }}
                  style={{ margin: "5px 0" }}
                >
                  Очистить
                </Button>
              </FormControl>
            </form>
          );
        }}
      </Formik>
    </div>
  );
}

export default App;
